.tab {
  position: fixed;
  z-index: 2000;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.5s;
  opacity: 0;
  visibility: hidden;
}
.tabContainer {
  width: 475px;
  height: 475px;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  transform: rotate(45deg);
}
.content {
  width: 100%;
  height: 100%;
  transform: rotate(-45deg);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.title {
  color: #fff;
}
.text {
  color: #fff;
  text-align: center;
  padding: 10px 60px;
}

.appear {
  opacity: 1;
  visibility: visible;
}
.disappear {
  opacity: 0;
  visibility: hidden;
}
